import React, { Component } from 'react';
import $ from "jquery";
import axios from "axios";
import Slider from 'rc-slider';
import 'rc-slider/assets/index.css';
import 'rc-tooltip/assets/bootstrap.css';
import './Listing.css';
import Tooltip from 'rc-tooltip';
const createSliderWithTooltip = Slider.createSliderWithTooltip;
const Range = createSliderWithTooltip(Slider.Range);
const Handle = Slider.Handle;
const wrapperStyle = { width: '90%', marginBottom: 15, marginLeft: 'auto', marginRight: 'auto' };
const handle = (props) => {
    const { value, dragging, index, ...restProps } = props;
    return (
      <Tooltip
        prefixCls="rc-slider-tooltip"
        overlay={value}
        visible={dragging}
        placement="top"
        key={index}
      >
        <Handle value={value} {...restProps} />
      </Tooltip>
    );
  };

const marks = {
    0: <strong>$0</strong>,
    90000: <strong>$90000</strong>
};
class FormPrice extends Component{
   
    constructor(props) {
        super(props);

        this.state = {
            value: {
                min: 0, max: 90000
            }
        };
    }

    render() {
        const { handleFormValue, price } = this.props;
        return(
            <div className="col-xs-12">
                <div className="row">
                    <form method="post">
                        <div className="col-md-3 col-sm-12">
                            <div className="form-inline tit-range">
                                <label className="top-indent">Price Range:</label>
                            </div>
                        </div>
                        <div className="col-md-9 col-sm-12">
                            <div className="form-group">
                                <div className="price-range price-range-wrapper">
                                    <div style={wrapperStyle}>
                                        <Range min={0} max={90000} defaultValue={[price[0],price[1]]} marks={marks} tipFormatter={price => `${price} $`} onAfterChange={value => handleFormValue(value)} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        )
    }

    Get_Current_Selected_Search_Data() {
        var data = {};
        var transtype = "";
        var current_page = 1;

        if ($('#address-map').val() !== "") {
            data.address = $('#address-map').val();
            data.city = $('#address-map').val().split(',')[0];
            data.state = $('#address-map').val().split(',')[1];
        }

        if ($('#com-type').val() !== "null") {
            data.transaction_type = $('#com-type').val();
            transtype = data.transaction_type
        }

        if ($('#com-min-sqft').val() !== "") {
            data.min_sqft = $('#com-min-sqft').val()
        }

        if ($('#com-max-sqft').val() !== "") {
            data.max_sqft = $('#com-max-sqft').val()
        }

        /*if ($('#com-price').val() !== "") {
            var price = $('#com-price').val().split(';');
            data.price_min = price[0];
            data.price_max = price[1];
        }*/

        if ($('#com-listingdate').val() !== "") {
            data.listing_date = $('#com-listingdate').val()
        }

        if ($('#com-keywords').val() !== "") {
            data.keywords = $('#com-keywords').val();
        }

        if ($('#com-reo').is(":checked")) {
            data.reo = $('#com-reo').val()
        }

        if ($('#com-shortsale').is(":checked")) {
            data.short_sale = $('#com-shortsale').val()
        }

        var property_features_selected = [];
        $('.com-property-types input[type=checkbox]').each(function () {
            if ($(this).is(":checked")) {
                property_features_selected.push($(this).attr('name'));
            }
        });

        if (property_features_selected !== "") {
            data.property_types = property_features_selected;
        }

        //data.order_by = $('.sort-order').val();
        data.page = current_page;
        return data;
    }
   
    requestServer(){
        //console.log(this.state);
        const API_URL = 'https://gooneworld.com/dataAPI/compropertee?';
        /*let data = {
            city : 'Miami',
            state : 'FL',
            page : this.state.page
        };*/
        let data = this.Get_Current_Selected_Search_Data();
        console.log(data);
        axios.get(API_URL,{params : data})
        .then((res)=>{
            if(res.status===200){
                res.data.Results.map(l=>{
                    this.state.Listings.push(l);
                })    
                this.setState({
                    TotalRecords : res.data.TotalRecords,
                    TotalPages : res.data.TotalPages,
                    CurrentPage : res.data.CurrentPage
                });
            }
        })
        .catch((err)=>{
            console.error(err);
        });
    }

    queryServer(){
        this.requestServer();
    }
}

export default FormPrice;