import React, { Component } from 'react';
/* import {connect} from 'react-redux'; */
import {Map, InfoWindow, Marker, GoogleApiWrapper,Polyline} from 'google-maps-react';
import Geocode from "react-geocode";

export class MapList extends Component {
  state  = {
    markers : [],
    boundary : [],
    bounds :[],
    showingInfoWindow: false,
    activeMarker: {},
    selectedPlace: {},
  }

  constructor(props){
    super(props);
    this.geoCodeAddresses = this.geoCodeAddresses.bind(this);
    this.showMarkers = this.showMarkers.bind(this);
  }

  onMarkerClick = (props, marker, e) =>
      this.setState({
        selectedPlace: props,
        activeMarker: marker,
        showingInfoWindow: true
      });

  componentDidUpdate(prevProps, prevState, snapshot) {
    if(prevProps!=this.props){
      console.log('new Props',this.props);
    }
  }

  geoCodeAddresses(listings){
    Geocode.setApiKey('AIzaSyBIIMCtAQw9VFVhzdRxDe9XROOGAqqY0ps');
    return listings.map((l) => {
      //Geocode.enableDebug();
      let _address = l.StreetNumber + ' ' + l.StreetDirPrefix +' '+ l.StreetName +' '+ l.UnitNumber + ' '+l.City + ' '+ l.StateOrProvince;
      Geocode.fromAddress(_address).then(response => {
        this.state.markers.push({
          address : _address,
          description : l.Remarks,
          location: response.results[0].geometry.location
        });
        this.state.boundary.push(response.results[0].geometry.location);
        this.state.bounds = new this.props.google.maps.LatLngBounds();
        for (var i = 0; i < this.state.boundary.length; i++) {
          this.state.bounds.extend(this.state.boundary[i]);
        }
      });
    });
  }

  showMarkers(){
    console.log('Show Markers Called');
    let m = [];
    this.state.markers.map((l) => {
      return (
          <Marker onClick={this.onMarkerClick}
                  name={l.address} position={ l.location } />
      );
    });
  }

  render() {
    return (
        <div style={{ height: '100vh', width: '100%' }}>
          <Map google={this.props.google} center={this.state.bounds[1]}  bounds={this.state.bounds} zoom={14}>
            <Polyline
                paths={this.state.boundary}
                strokeColor="#0000FF"
                strokeOpacity={0.8}
                strokeWeight={2} />
            {this.geoCodeAddresses(this.props.Listings)}
            {this.showMarkers()}
            <InfoWindow onClose={this.onInfoWindowClose}>
              <div>
                <h1>{this.state.selectedPlace.name}</h1>
              </div>
            </InfoWindow>
          </Map>
        </div>
    );
  }
}

const mapStateToProps = (state) => {
  const { searchResult, search } = state;
  return {
    search: search,
    result: searchResult || null
  }
}

export default GoogleApiWrapper({
  apiKey: 'AIzaSyBIIMCtAQw9VFVhzdRxDe9XROOGAqqY0ps'
})(MapList)
