import React, { Component } from 'react';
import { Radio } from 'antd';
import { Menu } from 'semantic-ui-react';

import './typeview.css';


class TypeViews extends Component {

    state = {
        value:'map'
    }
    handleChange = (e) =>{
        var el = e.target;
        this.setState({
            value:el.value
        });
        if(typeof this.props.handleView !== "undefined"){
            this.props.handleView(el.value);
        }
    }
    render() {
        const { handleView, listView, gridView, mapView } = this.props;
        let {value} = this.state;

        return  (
            <ul className="tab-links col-md-4 col-xs-12">
                <Menu.Item position="right" className="menu-type">
                    <Menu.Item>
                        <Radio.Group value={value} onChange={this.handleChange}>
                            <Radio.Button value="map"><i className="fa fa-map" aria-hidden="true"></i> Map</Radio.Button>
                            <Radio.Button value="grid"><i className="fa fa-table"></i>Grid</Radio.Button>
                            <Radio.Button value="list"><i className="fa fa-th-list"></i>List</Radio.Button>
                        </Radio.Group>
                    </Menu.Item>
                </Menu.Item>
				{/* <li className={mapView ? 'col-lg-3 col-lg-offset-2 col-md-4 col-xs-4 no-pad active' : 'col-lg-3 col-lg-offset-2 col-md-4 col-xs-4 no-pad'} onClick={ () => handleView('map') }><a href="javascript:" className="map2"><i className="fa fa-map"></i>Map</a></li>
				<li className={gridView ? 'col-lg-3 col-md-4 col-xs-4 no-pad active' : 'col-lg-3 col-md-4 col-xs-4 no-pad'} onClick={ () => handleView('grid') }><a href="javascript:"><i className="fa fa-table"></i>Grid</a></li>
				<li className={listView ? 'col-lg-3 col-md-4 col-xs-4 bdr-rgh no-pad active' : 'col-lg-3 col-md-4 col-xs-4 bdr-rgh no-pad'} onClick={ () => handleView('list') }><a href="javascript:"><i className="fa fa-th-list"></i>List</a></li> */}
			</ul>
        )
    }
}

export default TypeViews;