const Combo = (props) => {

    let {label,onChange,minChecked} = props;
    let defaultValue = props.defaultValue || [];
    let selection = (typeof props.selection !== "undefined")?(props.selection.length>0)?props.selection:defaultValue:defaultValue;
    let visible = props.visible || false;
    let data = props.data || [];

    let style = {
        minWidth:"320px",
        maxWidth:"400px"
    }

    if(props.size === 'small'){
        style = {
            minWidth:"200px",
            maxWidth:"400px"
        }
         
    }
    const onCheck = (e) => {
       console.log(e.target);
    }
    const handleChange = (values) => {
        if(values.length>0)
        selection = values;
        if(typeof minChecked !== "undefined")
            if(selection.length <= minChecked){
                return onChange(selection);
            }
        if(typeof onChange !== "undefined") onChange(values);
    }
    var menu = props.menu || (
        <Menu className="property-type-menu" style={{...style}}>
            <Menu.Item>
                <Checkbox.Group style={{ width: '100%' }} onChange={handleChange} defaultValue={defaultValue}>
                    <Row >
                        {
                            data.map((item,index) => {
                                return (<Col key={index} span={12} style={{paddingLeft:'0px',paddingRight:'0px'}}>
                                    <Checkbox 
                                    onChange={onCheck} 
                                    value={item.value}  
                                    >{item.text}</Checkbox>
                                </Col>)
                            })
                        }
                    </Row>
                </Checkbox.Group>
            </Menu.Item>
        </Menu> 
    );

    return (<Dropdown 
        style={{...props.style}}
        overlay={menu} 
        overlayStyle={{width:'auto'}}
        visible={visible}
        trigger={['click']}
        onVisibleChange={(e)=>{
            visible = !visible;
            if(typeof props.onClick !== "undefined"){
                props.onClick();
            }
        }}
        onClick={() => { 
            visible = !visible;
            if(typeof props.onClick !== "undefined"){
                props.onClick();
            }
        }}>
        <Button style={{ margin: 0, width:'100%' }}>
            {label} <Icon type="down" />
        </Button>
    </Dropdown>);
}

