    search = (params) => {
        /* Search API */
        api_search({params})
        .then((res) => {
            if(res.status === 200) {
                const {  TotalPages, TotalRecords, CurrentPage } = res.data.meta;
                const response = res.data;
                const Listings = response.data;
                this.setState({
                    TotalRecords,
                    TotalPages,
                    CurrentPage,
                    loading: false,
                    Listings,
                    page: (this.state.page + 1)
                });
            }
        })
        .catch((err)=>{
            console.error(err);
        });
    }
    
    onChangeListingType = (values) => {
        let {filters} = this.state;

        console.log(values);

        this.setState({
            filters:{
                ...filters,
                "listing_type":values
            }
        });
    }
    goToSearch = (value,option) => {
        const { history } = this.props;
        const { url } = this.props.match;
        let { address, type } = this.state;

        let {props:{category,record}} = option;
        let {City,State} = record;

        let path = ``;
        let location = url.split('/');
        if(address) {
            address = address.split(',');
        
            location[3] = City || 'Miami';
            location[4] = State.replace(/ /g,"") || 'FL';

            location.forEach(loc => {
                if(loc !== '') {
                    path = `${path}/${loc}`;
                }
            });
            
            this.props.actions.setSearchParam({
                address,
                limit:30,
                City,
                State
            });
            history.push(path);
        }
    }

    sortByChange = (e) => {
        let {filters} = this.state;
        let value = e.key;
        filters["order_by"] = value;
        this.state.filters = {
            ...filters
        }
        this.filter("orderBy");
    }
    handleTransactionType = e => {
        let {filters} = this.state;
        let {value} =  e.target;
        filters["transactionType"] = value;
        this.state.filters = {
            ...filters
        }
        this.filter("transactionType");
    }
    handlePrice = (value,name) => {
        let {filters} = this.state;
        filters[name] = value;
        if((name === 'PriceTo' && filters["PriceFrom"]) || (name === 'PriceFrom' && filters["PriceTo"])){
            this.setState({
                ...filters
            });
            this.filter("Price");
        }
    }
    handleSqFt = (value,name) => {
        let {filters} = this.state;
        filters[name] = value;
        this.setState({
            ...filters
        });
        if(filters["ApproximateLotSizeTo"] && filters["ApproximateLotSizeFrom"]){
            this.filter("LotSize");
        }else if(!filters["ApproximateLotSizeTo"] && !filters["ApproximateLotSizeFrom"]){
            this.filter("LotSize",{
                ApproximateLotSizeTo:undefined,
                ApproximateLotSizeFrom:undefined
            });
        }
    }
    handleSqArea = (value,name) => {
        let {filters} = this.state;
        filters[name] = value;
        this.setState({
            ...filters
        });
        if(filters["ApproxSqftTotalAreaTo"] && filters["ApproxSqftTotalAreaFrom"]){
            this.filter("SqFt");
        }else if(!filters["ApproxSqftTotalAreaTo"] && !filters["ApproxSqftTotalAreaFrom"]){
            this.filter("SqFt",{
                ApproxSqftTotalAreaTo:undefined,
                ApproxSqftTotalAreaFrom:undefined
            });
        }
    }
    handleListingDate = (date,value) => {
        let {filters} = this.state;
        filters["ListingContractDate"] = value;
        this.setState({
            ...filters
        });
        if(value){
            this.filter("ListingDate");
        }else{
            this.filter("ListingDate",{
                ListingContractDate:undefined
            });
        }
    }
    handleKeywords = (e) => {
        const { value } = e.target;
        
        let {filters} = this.state;
        filters["Amenities"] = value;
        this.setState({
            ...filters
        });
        if(value){
            this.filter("keywords");
        }else{
            this.filter("keywords",{
                Amenities:undefined
            });
        } 
    }
    handleREOYN = e => {
        let {value,checked,name} = e.target;
        let {filters} = this.state;
        this.filter(name,{
            [name]:(checked)?"1":undefined
        });        
    }
    
    filter = (type,extra) => {

        extra = extra || {};

        const { history } = this.props;
        let  { params,url } = this.props.match;
        let {filters} = this.state;
        let data = this.props.search.data;

        switch(type){
            case "propertyType":
                let match = url.match(/(propertyType\=)((\w)+\&?)+/g);

                /* 
                * AQUI FILTRO DINAMICO    
                */
               /*  history.push({
                    pathname: url,
                    params:{
                        transaction_type:"for_lease",
                        "propertyType":filters.propertyType
                    },
                    search: `?propertyType=${filters.propertyType.join(",")}`
                });  */

                /* if(!match){
                    return console.log("No match!");
                } */
                
                this.props.actions.changeSearch({
                    ...data,
                    "TypeofProperty":(filters.TypeofProperty.join(",")!=="")?filters.TypeofProperty.join(","):undefined,
                    "propertyType":(filters.propertyType)?filters.propertyType:undefined,
                    "transactionType":this.state.transactionType,
                    
                    ApproximateLotSizeFrom:undefined,
                    ApproximateLotSizeTo:undefined,
                    
                    ApproxSqftTotalAreaFrom:undefined,
                    ApproxSqftTotalAreaTo:undefined,
                    page:1
                });
            break;
            case "transactionType":
                this.props.actions.changeSearch({
                    ...data,
                    page:1,
                    "transactionType":filters.transactionType
                });
            break;
            case "Price":
                this.props.actions.changeSearch({
                    ...data,
                    page:1,
                    "PriceFrom":filters.PriceFrom,
                    "PriceTo":filters.PriceTo
                });
            break; 
            case "LotSize":
                this.props.actions.changeSearch({
                    ...data,
                    page:1,
                    ApproxSqftTotalAreaFrom:undefined,
                    ApproximateLotSizeTo:undefined,
                    "ApproximateLotSizeFrom":filters["ApproximateLotSizeFrom"],
                    "ApproximateLotSizeTo":filters["ApproximateLotSizeTo"],
                    ...extra
                });  
            break;
            case "SqFt":
                this.props.actions.changeSearch({
                    ...data,
                    page:1,
                    ApproximateLotSizeFrom:undefined,
                    ApproximateLotSizeTo:undefined,
                    "ApproxSqftTotalAreaFrom":filters["ApproxSqftTotalAreaFrom"],
                    "ApproxSqftTotalAreaTo":filters["ApproxSqftTotalAreaTo"],
                    ...extra
                }); 
            break;
            case "ListingDate":
                this.props.actions.changeSearch({
                    ...data,
                    page:1,
                    "ListingContractDate":filters["ListingContractDate"],
                    ...extra
                });
            break;
            case "keywords":
                this.props.actions.changeSearch({
                    ...data,
                    page:1,
                    "Amenities":filters["Amenities"],
                    ...extra
                });
            break;
            case "REOYN":
                this.props.actions.changeSearch({
                    ...data,
                    page:1,
                    ...extra
                });
            break;
            case "ShortSaleYN":
                this.props.actions.changeSearch({
                    ...data,
                    page:1,
                    ...extra
                });
            break;
            case "orderBy":
                this.props.actions.changeSearch({
                    ...data,
                    "order_by":filters["order_by"]
                }); 
            break;
            default:
                this.props.actions.changeSearch({
                    ...filters
                });
            break;

        }
    }
    handleSaveSearch = e => {
        let {search:{data},history} = this.props;
        if(typeof localStorage.userId === "undefined"){
            return history.push("/dashboard/login",{
                state:data
            });
        }
        data["userId"] = localStorage.userId;
        data["hostApp"] = "CP";
        save_search(data)
        .then(response => {
            let {data} = response;
            message.info("Saved successfully!");
        })
        .catch(err => message.error(err.message)) ;
    }
