    Get_Current_Selected_Search_Data() {
        var data = {};
        var transtype = "";
        var current_page = 1;

        if ($('#address-map').val() !== "") {
            data.address = $('#address-map').val();
            data.city = $('#address-map').val().split(',')[0];
            data.state = $('#address-map').val().split(',')[1];
        }

        if ($('#com-type').val() !== "null") {
            data.transaction_type = $('#com-type').val();
            transtype = data.transaction_type
        }

        if ($('#com-min-sqft').val() !== "") {
            data.min_sqft = $('#com-min-sqft').val()
        }

        if ($('#com-max-sqft').val() !== "") {
            data.max_sqft = $('#com-max-sqft').val()
        }

        /*if ($('#com-price').val() !== "") {
            var price = $('#com-price').val().split(';');
            data.price_min = price[0];
            data.price_max = price[1];
        }*/

        if ($('#com-listingdate').val() !== "") {
            data.listing_date = $('#com-listingdate').val()
        }

        if ($('#com-keywords').val() !== "") {
            data.keywords = $('#com-keywords').val();
        }

        if ($('#com-reo').is(":checked")) {
            data.reo = $('#com-reo').val()
        }

        if ($('#com-shortsale').is(":checked")) {
            data.short_sale = $('#com-shortsale').val()
        }

        var property_features_selected = [];
        $('.com-property-types input[type=checkbox]').each(function () {
            if ($(this).is(":checked")) {
                property_features_selected.push($(this).attr('name'));
            }
        });

        if (property_features_selected !== "") {
            data.property_types = property_features_selected;
        }

        //data.order_by = $('.sort-order').val();
        data.page = current_page;
        return data;
    }

