const TplSkeleton = (key) => {
  return (<div className="col-md-6 col-sm-6 col-xs-6 prop" key={key}>
      <div className="wht-cont">
        <Skeleton className="property-skeleton" active loading avatar={{ shape: 'square' }} title={false} paragraph={{ rows: 3 }}/>
      </div>
  </div>)
}



class PropertySearch extends Component{
    state = {
        TotalRecords : '',
        TotalPages : '',
        CurrentPage : '',
        Listings : [],
        data:[
        ],
        pagination:{
          CurrentPage:1,
          FetchedRecords:0,
          TotalPages:0,
          TotalRecords:0
        },
        gridView: false,
        listView: true,
        mapView: false,
        loading:false,
        preloading:true,
        hasMore: true,
        price: { min: 0, max: 90000 },
        page: 1,
        top:0
    }

    constructor(props)  {
        super(props);
        this.fetchData = debounce(this.fetchData,300);
        
        this.scrollListRef = React.createRef();
    }

    displaySkeleton = () => {
      let count = 6;
      return Array.apply(null, {length: count}).map(Number.call, Number)
    }
    componentDidMount() {
      /* const data = this.displaySkeleton();
      this.setState({
        data,
        loading:true
      }); */

      console.log("STORE: ",this.context,this.props);

      this.fetchData((res) => {
        this.setState({
          data: res.data,
          pagination:res.meta,
          preloading:false,
          loading:false
        });
      });
      
    }
    fetchData = (callback) => {

      let {page,preloading} = this.state;
      let  { params } = this.props.match;
      let {city,state,dynamicParams,transaction_type} = params;
     
      console.log("Porps:::",this.props.search);


      let data = this.props.search.data || {};
      
      data = {
        ...data,
        Address: `${city},${state}`,
        City:city,
        StateOrProvince:state,
        limit:30
      }
       
      
      
      this.setState({
        loading:true,
        preloading:true
      })
      console.log("Search: ",this.props,data);

       /* Search API */
       api_search({params:data})
       .then((res) => {
        var response = res.data;
        /*  this.state.page +=1; */
        callback(response);
       })
       .catch((err)=>{
           console.error(err);
       });
    }
    handleInfiniteOnLoad = () => {


      var $container = $("#scroll-container");
      var $scrollTo = $('.prop:last');


      const infiniteList = this.scrollListRef.current;    
      let data = this.state.data;
     // data = data.concat(infiniteList.displaySkeleton());
      this.setState({
        loading: true
      });
      

    return new Promise((resolve,reject) => {
      
        this.fetchData((res) => {
          //data = data.filter(item => (item.type !== "dummy")).concat(res.data);

          data = res.data;

          if (res.data.length == 0) {
            message.warning('There are no more properties to offer',10);
            this.setState({
              hasMore: false,
              loading: false,
            });
            return reject();
          }
         
          this.animation();
          
          this.setState({
            data,
            pagination:res.meta,
            loading: false,
          });
          return resolve(data);
        });
    });
    }
    animation = () => {
      var $container = $("#scroll-list");
      var $scrollTo = $('.prop:first');
      if(typeof $container !== "undefined"){
        if(typeof $scrollTo.offset() !== "undefined"){
          $container.animate({scrollTop: $scrollTo.offset().top - $container.offset().top + $container.scrollTop(), scrollLeft: 0},300);
        }
      }
    }
    getAddress = (item) => {
      var address = "";

      if (item.StreetNumber !== "No Data") {
          address += " " + item.StreetNumber;
      }
      if (item.StreetDirPrefix !== "No Data") {
          address += " " + item.StreetDirPrefix;
      }
      if (item.StreetName !== "No Data") {
          address += " " + item.StreetName;
      }
      if (item.UnitNumber !== "No Data") {
          address += " # " + item.UnitNumber;
      }
      return address;
    }
    getTypeProps = (item) => {
      var typeprop = '';

      if (item.StyleofProperty !== "" && item.StyleofProperty !== "No Data") {
          typeprop = item.StyleofProperty;
      } else if (item.TypeofProperty !== "" && item.TypeofProperty !== "No Data") {
          typeprop = item.TypeofProperty;
      }
      return typeprop;
    }
    handleFormValue = (value) => {
      this.state.page = 1;
      this.setState({ price: value, hasMore: true });
      this.fetchData((res) => {
        this.setState({
          data: res.data,
          pagination:res.meta
        });
      });
    }
    onChange = (page) => {
      console.log("Page: ",page);

      const infiniteList = this.scrollListRef.current;    
      let data = infiniteList.displaySkeleton();

      let search = this.props.search.data || {};

      this.props.actions.changeSearch({
          ...search,
          page
      });
      this.setState({
        page,data
      });
      this.fetchData((res) => {
        this.setState({
          data: res.data,
          pagination:res.meta,
          preloading:false
        });
      });

    }
    componentWillReceiveProps(nextProps, nextContext) {
      
      
      
      this.fetchData((res) => {
        
        console.log("changeData: ",res.data);


        this.setState({
          data: res.data,
          pagination:res.meta,
          preloading:false,
          loading:false
        });
      });
    }
    render() {
        const { Listings,view, mapView, gridView, listView, price, handleFormValue } = this.props;

        let {loading,preloading,data,pagination} =  this.state;

        return(
          <div className="tabs" style={{ width: '100%',height: '100%' }}>
            <div className="tab-content" style={{ width: '100%',height: '100%' }} >
            {
              (mapView || gridView || listView) &&
              <div id="tab1" className="tab" style={{ 'display': 'block',height: '100%' }}>
                {
                mapView &&
                
                  <Affix className="sidebar col-sm-5 col-xs-12 sidemap" style={{ 'display': 'block',height: '100%' }}>
                      <div id="map">
                          {
                            data.length > 0 && !preloading &&  <DrawingView key={data.length} Listings={data}/>
                          }
                      </div> 
                  </Affix>
                }
                <div className={(mapView)?"content col-sm-7 col-xs-12 view-container":"col-xs-12 content_2"} >
                    <div className={"col-md-12 clist"} style={{marginLeft:0, marginRight:0}}>
                    
                    {/* <FormPrice handleFormValue={this.handleFormValue} price={price}/> */}
                    <div className="wide-2" style={{height:'100vh',marginTop:'10px'}}>
                      <div className="col-xs-12 listing-container" >
                        <div className="row" id="prop-map" >
                          <InfiniteList 
                            ref={this.scrollListRef}
                            className={view}
                            loadMore={this.handleInfiniteOnLoad}
                            onChange={this.onChange}
                            loading={loading}
                            preloading={preloading}
                            data={data}
                            pagination={pagination}
                            hasMore={this.state.hasMore}
                            renderItem={(item) => ((listView)?<ListingList listingData={item}/>:<Listing listingData={item}/>)}
                            />
                          </div>
                        </div>
                      </div>
                </div>
                </div>
              </div>
            }
          </div>
        </div>
      )
    }
}

const mapStateToProps = (state) => {
  const { searchResult, search } = state;
    return {
      search: search,
      result: searchResult || null
    }
}

const mapDispatchToProps = (dispatch) => ({
    actions: bindActionCreators(actions,dispatch)
});



export default connect(mapStateToProps,mapDispatchToProps)(PropertySearch);