import React, { Component } from 'react';
import Listing from "./Listing";

class ViewGrid extends Component{
    state = {
    }
    
    constructor(props){
        super(props);
    }

    render(){
        const { Listings } = this.props;
        return  (
            <div className="wide-2">
                <div className="row" id="prop-grid">
                    {   Listings.length > 0 && Listings.map((l, index)=>
                        <Listing key={index} listingData={l}/>
                    )}
                </div>
            </div>
        )
    }
}

export default ViewGrid;