const setSearchParam = (data) =>{
    console.log("Actions: ",data);
    return {
        type: 'SET_SEARCH_PARAM',
        payload: data
    }
}

/* Custom Actions */
const changeSearch = data => {
    console.log("CITY_CHANGE:",data);
    return {
        type:'CITY_CHANGE',
        data
    }
}
export default { setSearchParam, changeSearch };