import React, { Component } from 'react';
import Listing from "./ListingList";

class ViewList extends Component{
    state = {}

    constructor(props){
        super(props);
    }
    
    render()  {
        const { Listings } = this.props;
        return(
            <div className="wide-2">
                <div className="row" id="prop-list">
                    {  Listings.length > 0 && Listings.map((l, index)=>
                        <Listing key={index} listingData={l}/>
                    )}
                </div>
            </div>
        )
    }
}

export default ViewList;