import React,{Component} from 'react';
import { Link } from 'react-router-dom';
import {RegisterForm} from '../../../components/com';
import {signup} from '../../../services'
import { Layout,Col,Row,message,Icon} from 'antd';

import './register.css';

const {
  Header, Footer, Sider, Content,
} = Layout;


class Register extends Component{
    constructor(props){
        super(props);

    }
    onSubmit = (err,params) => {
        let {history} = this.props;
        if(!err){
            if(params["password"] !== params["rp_password"]) return message.error("Passwords do not match!");
            delete params["rp_password"];
            signup(params)
            .then(response => {
                let {data} = response;
                console.log("Response: ",data);
                history.push("/dashboard/login");
            })
            .catch(err => message.error(err.message));
        }
    }
    render(){
        return(
            <section id="wrapper" className="login-register">
                <div className="login-box login-sidebar">
                    <div className="white-box">
                        <div className="conteim">
                            <a href="/" class="db titlogo">CP</a>
                        </div>
                        <RegisterForm  formLayout="vertical" onSubmit={this.onSubmit}/>
                    </div>
                </div>
                {/*<Layout className="register-page">
                        <Header>Create an Account</Header>
                        <Content>
                            <Row className="register-container">
                                <Col span={8}></Col>
                                <Col span={8}>
                                <Col span={8}></Col>
                            </Row>    
                        </Content>
                        <Footer>Compropertee 2019</Footer>
                    </Layout>*/}
            </section>
        );
    }
}
export default Register;