import React, { Component } from 'react';
import $ from "jquery";
import "./Header_Listing.css";	
import { withRouter } from 'react-router'
import {logout} from '../../services';
import { Avatar, Dropdown,Menu,Icon,message,Tabs } from 'antd';
import {MyModal,ProfileForm,ChangePasswordForm} from '../com'
import {
	BrowserView,
	MobileView,
	isBrowser,
	isMobile
  } from "react-device-detect";

const MyMenu = props => {
	let {handleMenuClick} = props;
	return (
		<Menu onClick={handleMenuClick}>
			<Menu.Item key="profile">
				<Icon type="user" />
				Profile
			</Menu.Item>
			<Menu.Item key="dashboard">
				<Icon type="dashboard" />
				Dashboard
			</Menu.Item>
			<Menu.Item key="logout">
				<Icon type="close" />
				Log Out
			</Menu.Item>
		</Menu>
	);
}
const TabPane = Tabs.TabPane;
class Header_Listing extends Component{

	constructor(props){
		super(props);
		this.state = {
			userId:localStorage.userId,
			visible:false
		}
	}
	componentDidMount() {

        var contador = 1;
        $('.logo_mob_text').click(function(){
            if (contador == 1) {
                $('nav').animate({
                    left: '0'
                });
                contador = 0;
            } else {
                contador = 1;
                $('nav').animate({
                    left: '-100%'
                });
            }
        });

        $('.submenu').click(function(){
            $(this).children('.children').slideToggle();
        });
    
    }
    handleMenuClick = item => {
		let {key} = item;
		let {history} = this.props;
		let {userId} = this.state;
		switch(key){
			case "logout":
				
				/* if(history){
					localStorage.clear();
					history.push("/dashboard/login");
				} */
				logout({
						"access_token":localStorage.access_token
					})
					.then(response => {
						let {data} = response;
						localStorage.clear();
						history.push("/dashboard/login");
					})
					.catch(err => message.error(err.message));
			break;
			case "profile":
					this.setState({
						visible:true
					});
			break;
		}
	}
    render(){
		let {userId,visible} = this.state;
		return(
            <header className="header_listing"> 
				<div className="menu_bar">
					<div className="logo_mob_text">CP</div>
					<div className="clearfix"></div>
				</div>
			    <nav className="menu_nav">
					<ul>
						<li><a href="/" className="lma">Home</a></li>
						<li>
							<a className="lma" href="/listings/search/Miami/FL/for_sale" target="_blank">For Sale</a>
				        </li>
				        <li>
							<a className="lma" href="/listings/search/Miami/FL/for_lease" target="_blank">For Rent</a>
				        </li>
				        {
							(typeof userId !== "undefined") &&
							<li>
								<a className="lma" href="/dashboard/Login">Add Listing</a>
							</li>
						}
						{
							(typeof userId === "undefined") &&
							<li>
								<a className="lma" href="/dashboard/Login">Sign In</a>
							</li>
						}
						{
							(typeof userId === "undefined") &&
							<li>
								<a className="lma" href="/dashboard/register">Sign Up</a>
							</li>
						}
						{
							(typeof localStorage.username !== "undefined") &&
							<li>
								<BrowserView>
									<Dropdown  overlay={<MyMenu handleMenuClick={this.handleMenuClick}/>}>
										<Avatar className="avatar" 
										style={{ color: '#f56a00', backgroundColor: '#fde3cf' }}
										
										>{
											localStorage.username.substring(0,1).toUpperCase()
										}</Avatar>
									</Dropdown>
								</BrowserView>
								<MobileView>
									<li>
										<a className="lma" onClick={() => this.handleMenuClick({key:"profile"})}>
											<Icon type="user" />Profile
										</a>
									</li>
									<li>
										<a className="lma">
											<Icon type="dashboard" onClick={() => this.handleMenuClick({key:"dashboard"})}/> Dashboard
										</a>
									</li>
									<li>
										<a className="lma" onClick={() => this.handleMenuClick({key:"logout"})}>
											<Icon type="close" /> Log Out
										</a>
									</li>
								</MobileView>
							</li>
						}
						
					</ul>
					<MyModal title={
						<span>
							<Icon type="user" /> USER PROFILE
						</span>} visible={visible} width={400} onCancel={visible => this.setState({visible})}>
						<Tabs type="card">
							<TabPane tab="User Data" key="1">
								<ProfileForm onSubmit={() => this.setState({visible:false})}/>
							</TabPane>
							<TabPane tab="Security" key="2">
								<ChangePasswordForm onSubmit={() => this.setState({visible:false})}/>
							</TabPane>
						</Tabs>
					</MyModal>	
				</nav>
	</header>
        );
    }
}

export default withRouter(Header_Listing);